<template>
	<view class="box">
		<view class="address-box send-box">
			<!-- 是否要配送 （是否是虚拟商品） -->
			<template v-if="order_confirm.needLogistic=='Y'">
				<view class="send-type" @tap="receive_mode='PEI_SONG'">
					<view class="left">
						<view class="address-text">
							送货上门
						</view>
					</view>
					<view class="iconfont iconlujing" :class="{'iconxuanzhong':receive_mode=='PEI_SONG'}"></view>
				</view>
			</template>
			<template v-if="order_confirm.orderType=='COUPON'">
				<view class="send-type" @tap="receive_mode='JI_SHOU'">
					<view class="left">
						<view class="address-text">
							寄售
						</view>
					</view>
					<view class="iconfont iconlujing" :class="{'iconxuanzhong':receive_mode=='JI_SHOU'}"></view>
				</view>
			</template>

		</view>
		<view v-show="receive_mode=='PEI_SONG'">
			<navigator url='../member/address_list?from=order' class="address-box no-address" v-if="!order_address.consignee">
				<view class="left">
					<view class="iconfont icondizhidingwei"></view>
					<view class="address-text">
						选择收货地址
					</view>
				</view>
				<view class="iconfont iconxiayibu"></view>
			</navigator>
			<view class="adress-box" v-if="order_address.consignee">
				<view class="name-box font-bold">
					<text class="name">
						{{order_address.consignee}}
					</text>
					<text class="phone">
						{{order_address.mobile}}
					</text>
				</view>
				<view class="address">
					<view class="text">
						{{order_address.address1}}{{order_address.address2}}
					</view>
					<navigator url='../member/address_list?from=order' class="edit">
						修改
						<text class="iconfont iconxiayibu"></text>
					</navigator>
				</view>
			</view>
		</view>
		<view class="dott_line" v-show="receive_mode=='PEI_SONG'"></view>
		<template v-for="(item,k) in order_confirm.orderHeadShopList">
			<view class="main-box" :key="k">
				<view class="shop-name">
					<view class="iconfont icondianpu1"></view>
					<view class="name">{{item.merchantShopName}}</view>
				</view>
				<template v-for="(val,i) in item.orderDetList">
					<view class="goods-box" :key="i">
						<image lazy-load class="img scale11" :src="val.skuUrl" mode="aspectFill"></image>
						<view class="right">
							<view class="top">
								<view class="title">
									{{val.skuName}}
								</view>
								<view class="types-box">
									<view class="type">{{val.specNames}}</view>
									<view class="num">X {{val.sl}} </view>
								</view>
							</view>
							<view class="price bold">
								<text v-if="order_confirm.orderType!=='COUPON'&&order_confirm.orderType!=='ACTIVE_POINT'">￥</text>
								{{val.orginPrice | formatNum}}
								<text class="font-small" v-if="order_confirm.orderType=='COUPON'">团批劵</text>
								<text class="font-small" v-else-if="order_confirm.orderType=='ACTIVE_POINT'">积分</text>

							</view>
						</view>
					</view>
				</template>

				<view class="line-box">
					<view class="line">
						<view class="title">
							配送方式
							<text class="send">{{receive_map[receive_mode]}}</text>
						</view>
						<template v-if="receive_mode=='PEI_SONG'">
							<view class="price" v-if="item.logisticPayType=='CUSTOMER'">
								￥{{item.logisticsFee | formatNum}}
							</view>
							<view class="price" v-else>
								免运费
							</view>
						</template>

					</view>
					<view class="line" style="display: none;">
						<view class="title">
							优惠活动
						</view>
						<view class="price">
							满400减50
						</view>
					</view>
					<view class="line all-line">
						<view class="title">
							订单备注
						</view>
						<textarea class="remark" v-model="item.remarks" placeholder="和商家协商一致后留言，最多50个字" />
						</view>
					<view class="line">
						<view class="title">
						</view>
						<view class="price">
							<text class="num">共{{item.orderDetList.length}}件</text>
							<text>小计：</text>
							<text class="total-price font-bold">
								<text class="total-price font-bold">
									<text v-if="!$common.isAorC(order_confirm.orderType)">￥</text>
									<!-- 团批劵 -->
									<template v-if="order_confirm.orderType=='COUPON'">
										{{item.couponMoney | formatNum}}
									</template>
									<!--活动积分 -->
									<template v-else-if="order_confirm.orderType=='ACTIVE_POINT'">
										{{item.jfMoney | formatNum}}
									</template>
									<!--其他 -->
									<template v-else>
										{{item.totalMoney | formatNum}}
									</template>
									<text class="font-small" v-if="$common.isAorC(order_confirm.orderType)">{{money_text[order_confirm.orderType]}}</text>
								</text>
							</text>
						</view>
					</view>
				</view>
				<button open-type="contact" class="kefu no-btn">
					<view class="border-btn border-gray">联系客服</view>
				</button>
			</view>
		</template>
		<view class="main-box line-box" style="display: none;">
			<view class="line" style="margin:0">
				<view>可用金币抵扣8元</view>
				<view class="iconfont iconlujing iconxuanzhong"></view>
			</view>
		
		</view>
		
		<view class="main-box" v-if="!$common.isAorC(order_confirm.orderType)">
			<view class="pay-title">
				支付方式
			</view>
			<view class="pay-list">
				<view class="iconfont iconweixinzhifu "></view>
				<view class="iconfont iconlujing iconxuanzhong"></view>
			</view>

		</view>
		<view class="blank"></view>
		<view class="fix-box">
			<view class="left">
				总计：<text class="price font-bold">
					<text v-if="order_confirm.orderType!=='COUPON'&&order_confirm.orderType!=='ACTIVE_POINT'">￥</text>
					<!-- 团批劵 -->
					<template v-if="order_confirm.orderType=='COUPON'">
						{{order_confirm.couponMoney | formatNum}}
					</template>
					<!--活动积分 -->
					<template v-else-if="order_confirm.orderType=='ACTIVE_POINT'">
						{{order_confirm.jfMoney | formatNum}}
					</template>
					<!--其他 -->
					<template v-else>
						{{order_confirm.totalMoney | formatNum}}
					</template>
					<text class="font-small" v-if="order_confirm.orderType=='COUPON'">团批劵</text>
					<text class="font-small" v-else-if="order_confirm.orderType=='ACTIVE_POINT'">积分</text>
					</text>
			</view>
			<view class="bg-btn active" @tap="settle">
				去结算
			</view>
		</view>
	</view>
</template>

<script>
	// ZI_TI 自提,PEI_SONG 配送，OFFLINE_SCAN 线下扫码,JI_SHOU 寄售
	import {mapState} from 'vuex';
	export default {
		data() {
			return {
				receive_mode:"PEI_SONG",
				default_address:{},
				receive_map:{
					'ZI_TI':'自提',
					'PEI_SONG':'物流配送',
					'OFFLINE_SCAN':'线下扫码',
					'JI_SHOU':'寄售',
				},
				money_text:{
					'ACTIVE_POINT':'积分',
					'COUPON':'团批劵',
					},
				is_loading:false
			}
		},
		onLoad(option) {
			this.loadAddress();
		},
		onShow() {
			if(this.order_confirm.orderType=='COUPON'){
				// 团批
				this.receive_mode="JI_SHOU"
			}else{
				if(this.order_confirm.needLogistic=='Y'){
					this.receive_mode="PEI_SONG"
				}else {
					this.receive_mode='OFFLINE_SCAN'
				}
			}
		},
		computed:{
			...mapState({
				order_confirm:state=>state.order_confirm,
				order_address:state=>state.order_address
			})
		},
		methods: {
			// 点击结算
			settle() {
				if(this.is_loading) return;
				for(let item of this.order_confirm.orderHeadShopList){
					item.receiveMode=this.receive_mode
				}
				// 物流配送
				if(this.receive_mode=='PEI_SONG'){
					// 没有选择收货地址
					if(!this.order_address.address1){
						this.$box.confirm('请先选择收货地址',false);
						return
					}
					this.order_confirm.orderReceiver={
							"address1":this.order_address.address1,
						    "address2": this.order_address.address2,
						    "receiverMobileNo": this.order_address.mobile,
						    "receiverName": this.order_address.consignee,
					}	
				}else{
					this.order_confirm.orderReceiver=null
				}
				this.is_loading=true;
				this.$api.post('/o2oShopingCart/genOrder',this.order_confirm).then(res=>{
					this.is_loading=false;
					if(res.code==200){
						let data=res.data;
						// 团批劵购买
						if(this.order_confirm.orderType=='ACTIVE_POINT'||this.order_confirm.orderType=='COUPON'){
							this.$box.toast('下单成功','success')
							setTimeout(function(){
								uni.reLaunch({
									url:'/pages/member/order_detail?sheetId='+data.sheetId
								})
							},600)
						}else {
							// 微信支付
							this.weixinPay(data.seqId,data.sheetId)
						}
							
					}
				}).catch(e=>{
					this.is_loading=false;
				})
			},
			// 微信支付
			weixinPay(seqId,sheetId) {
				// 团批专区跟活动专区商品
				if(this.order_confirm.orderType=='COUPON'||this.order_confirm.orderType=='ACTIVE_POINT'){
					return
				}
				// #ifdef MP 
				let that=this;
						that.$api.post('/payInfo/submitOrderPay',{
							  "orderHeadSeqId": seqId,
							  "type": 1
							}).then(res=>{
								if(res.code==200){
									let paymentData = res.data;
									uni.requestPayment({
									    timeStamp: paymentData.timeStamp,
									    nonceStr: paymentData.nonceStr,
									    package: paymentData.package,
									    signType: 'MD5',
									    paySign: paymentData.paySign,
									    success: (res) => {
									        uni.showToast({
									            title: "支付成功"
									        })
									    },
									    fail: (res) => {
									        uni.showModal({
									            content: "支付失败",
									            showCancel: false
									        })
									    },
									    complete: (res) => {
											console.log(res);
											// 跳转到订单详情
											setTimeout(function(){
												uni.reLaunch({
													url:'/pages/member/order_detail?sheetId='+sheetId
												})
											},600)
											
									    }
									})
								}else if(res.code==10012){
									uni.navigateTo({
										url:'/pages/login/login_wechat'
									})
								}
							})
					   // #endif
			},
			// 获取配送地址
			loadAddress(){
				this.$api.get('/o2oShippingAddress/findPage',{params:{
					customerSeqId:this.$common.getLocalSync('customerSeqId'),
					pageno:1,
					pagesize:1
				}}).then((res) => {
					if(res.code&&res.code!=200){return}
					if(res.list.length>0){
						// 默认为默认地址
						this.$store.commit('saveAddress',res.list[0])
					}
				})
			}
		},
		onUnload() {
			this.$store.commit('saveOrderConfirm', {})
			this.$store.commit('saveAddress', {})
		}
	}
</script>

<style lang="scss">
	
	.bg-btn {
		background: linear-gradient(90deg, rgba(255, 84, 10, 1) 0%, rgba(255, 202, 46, 1) 100%);
	}

	.iconxuanzhong {
		color: $color-money !important;
	}

	.box {
		width: 100%;
		overflow: hidden;

		.address-box {
			width: calc(100% - 68rpx);
			padding: 0 34rpx;
			display: flex;
			justify-content: space-between;
			align-items: center;
			background: white;
			margin: 40rpx 0;
			margin-bottom: 0;
			
			.name-box {
				font-size: 30rpx;

				.name {
					margin-right: 50rpx;
				}
			}


			.left {
				display: flex;
				align-items: center;
			}

			.iconfont {
				margin-right: 30rpx;
				font-size: 40rpx;
			}

			.iconxiayibu {
				margin: 0;
			}

			.address-text {
				font-size: 30rpx;
			}
		}
		.no-address {
			height: 180rpx;
		}

		.adress-box {
			width: calc(100% - 68rpx);
			height: 180rpx;
			padding: 0 34rpx;
			margin: 40rpx 0;
			margin-bottom: 0;
			display: flex;
			justify-content: space-between;
			align-items: center;
			flex-wrap: wrap;
			background: white;

			.name-box {
				width: 100%;
				font-size: 30rpx;
				margin: 30rpx 0;

				.name {
					margin-right: 50rpx;
				}
			}

			.address {
				width: 100%;
				font-size: 30rpx;
				color: #555555;
				display: flex;
				justify-content: space-between;
				align-items: center;
				font-size: 24rpx;
				margin-bottom: 50rpx;
				.text {
					width: 530rpx;
					flex-shrink: 0;
					font-size: 26rpx;
					overflow: hidden;
					white-space: nowrap;
					text-overflow: ellipsis;
				}

				.edit {
					color: #999;

					.iconfont {
						font-size: 20rpx;
						margin-left: 14rpx;
					}
				}
			}
		}

		.dott_line {
			width: 100%;
			height: 6rpx;
			background: url('~@/static/images/address-bg.png') repeat-x;
			background-size: 100% auto;
			margin-bottom: 40rpx;
		}

		.order-tips {
			width: calc(100% - 80rpx);
			height: auto;
			margin: 0 auto;
			padding: 30rpx 20rpx;
			background: $color-primary;
			border-radius: 10rpx;
			color: white;
			font-size: 30rpx;

			.time {
				font-size: 24rpx;
				margin-top: 30rpx;
			}
		}

		.main-box {
			padding: 30rpx 34rpx;
			padding-bottom: 20rpx;
			background: white;
			margin: 30rpx 20rpx;
			border-radius: 10px;

			.shop-name {
				display: flex;
				align-items: center;
				margin-bottom: 40rpx;

				.iconfont {
					font-size: 40rpx;
					color: #999;
					margin-right: 16rpx;
				}

				.name {
					font-size: 26rpx;
					font-weight: 500;
					color: rgba(16, 16, 16, 1);
					line-height: 30rpx;
				}
			}

			.goods-box {
				width: 100%;
				height: 200rpx;
				display: flex;
				overflow: hidden;
				margin-bottom: 20rpx;

				.img {
					width: 200rpx;
					height: 200rpx;
					border-radius: 8rpx;
					margin-right: 26rpx;
					flex-shrink: 0;
				}

				.right {
					display: flex;
					flex-wrap: wrap;
					align-content: space-between;
					padding: 6rpx 0;
					flex-grow: 1;

					.top {
						width: 100%;
					}

					.title {
						width: 100%;
						font-size: 30rpx;
						overflow:hidden;
						text-overflow: ellipsis;
						display: -webkit-box;
						-webkit-line-clamp: 2; 
						-webkit-box-orient: vertical;
					}

					.types-box {
						display: flex;
						width: 100%;
						justify-content: space-between;

						color: #999999;
						margin-top: 30rpx;

						.type {
							font-size: 20rpx;
						}

						.num {
							font-size: 26rpx;
						}
					}

					.price {
						color: $color-primary;
						font-size: 32rpx
					}
				}
			}

			.pay-title {
				font-size: 30rpx;
			}

			.pay-list {
				width: 100%;
				display: flex;
				justify-content: space-between;
				align-items: center;
				font-size: 36rpx;
				margin: 16rpx 0;

				.iconweixinzhifu {
					font-size: 50rpx;
					color: rgb(9, 187, 7);
				}
			}
		}
	}

	.kefu {
		display: flex;
		justify-content: flex-end;

		.border-btn {
			width: 176rpx;
			height: 74rpx;
			line-height: 74rpx;
			padding: 0;
			color: #666;
		}
	}

	.line-box {
		

		.line {
			display: flex;
			justify-content: space-between;
			margin: 30rpx 0;
			font-size: 26rpx;

			.send {
				color: #999999;
				margin-left: 60rpx;
			}

			.num {
				color: #999999;
				margin-right: 26rpx;
			}

			.total-price {
				font-size: 30rpx;
				color: $color-money;
			}

			.msg {
				font-size: 20rpx;
				color: #999;
			}

		}
		.all-line {
			flex-wrap: wrap;
			.title {
				width: 100%;
			}
			.remark {
				width: 100%;
				height: 160rpx;
				font-size: 26rpx;
				line-height: 1.5;
				margin: 10rpx 0;
			}
		}
	}

	.fix-box {
		justify-content: space-between;

		.left {
			font-size: 24rpx;

			.price {
				color: $color-money;
				font-size: 32rpx;
			}
		}
	}
	.box {
		.send-box {
			flex-wrap: wrap;
			.send-type {
				width: 100%;
				display: flex;
				justify-content: space-between;
				margin: 10rpx 0;
			}
		}
	}
	
</style>
